.piece {
  display: block;
  height: 100%;
  left: 0;
  top: 0;
  width: 100%; }

.noclick {
  pointer-events: none; }

.frame {
  background-color: transparent;
  border-radius: 2rem;
  border: 1rem solid;
  border-bottom-color: #121212;
  border-left-color: #080808;
  border-right-color: #080808;
  border-top-color: #020202;
  box-shadow: inset 0 0 18rem black, inset 0 0 3rem black, 0 0 10rem black;
  height: 96%;
  position: absolute;
  top: 2%;
  left: 2%;
  width: 96%;
  pointer-events: none; }

.emblem {
  display: none;
  position: absolute;
  left: 50%;
  bottom: 0;
  font-size: 2rem;
  height: 3rem;
  width: 3rem;
  text-align: center;
  color: white;
  background: -webkit-linear-gradient(#fff, #555);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  text-shadow: 0 0.1rem 0rem rgba(0, 0, 0, 0.4), 0 0 2rem rgba(0, 0, 0, 0.8);
  -webkit-transform: translate(-50%);
  transform: translate(-50%); }

.output {
  overflow: hidden;
  -webkit-animation: crt-output 10ms infinite;
  animation: crt-output 10ms infinite;
  background-color: #031e11;
  overflow: scroll;
  position: absolute;
  padding: 3rem 2rem;
  pointer-events: auto;
  text-shadow: 0rem 0.2rem 1rem #0c7b46;
  z-index: -1; }

@-webkit-keyframes crt-output {
  0% {
    opacity: 0.9; }
  50% {
    opacity: 1; } }

@keyframes crt-output {
  0% {
    opacity: 0.9; }
  50% {
